.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: 1em;
    line-height: 150%;
    font-weight: 700;
    margin-bottom: .625em;
  }
  input,
  textarea{
    border-radius: 0.4375em;
    border: 1px solid $white;
    padding: 0 .5em;
    height: 2.875em;
    width: 100%;
    font-size: 1em;
    background-color: $white;
    color: $black;
    box-shadow: $shadow;
    &::placeholder {
      color: $secondary;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
  .m-check{
    margin-bottom: .625em;
  }
  &._type2{
    label{
      font-weight: 400;
      line-height: 100%;
      margin-bottom: .4375em;
    }
  }
}

.date-input{
  input{
    background-image: url(../img/calendar.svg);
    background-size: 1.5em auto;
    background-repeat: no-repeat;
    background-position: right .5em center;
  }
}

.check-group{
  .m-check{
    margin-top: .625em;
  }
}

.pass-toggle{
  position: absolute;
  bottom: .8125em;
  right: 1.25em;
  width: 1.25em;
  height: 1.25em;
  @include center;
  svg,
  img{
    width: 100%;
    height: auto;
  }
  &._active{
    g{
      opacity: 1;
    }
  }
}

.filter-select__option,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 1.5625em;
    min-height: 1.3125em;
    font-weight: 400;
    margin: 0;
    span{
      font-size: .875em;
      line-height: 150%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border: 1px solid $black;
      width: .9375em;
      height: .9375em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        border-color: $primary;
        background-size: .6875em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .25em;
      background-image: url(../img/check.svg);
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 7.5625rem;
  display: grid;
  grid-template-columns: 2.1875em calc(100% - 2.1875em * 2) 2.1875em;
  border: 1px solid #E9EDF2;
  height: 3.125em;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    border-left: 1px solid #E9EDF2;
    border-right: 1px solid #E9EDF2;
    color: $black;
    font-weight: 500;
    font-size: 1em;
    text-align: center;
  }
  svg{
    width: .5em;
    height: auto;
  }
  button{
    @include center;
    &:hover{
      background-color: #E6E6EC;
    }
  }
}


