.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        display: inline-block;
        padding: 2em;
        border-radius: 1.25em;
        background-color: $white;
        box-shadow: $shadow;
        width: 45.25em;
        color: $black;
    }
    &__top{
        padding-right: 1.5em;
        margin-bottom: 2em;
    }
    .close-btn{
        position: absolute;
        top: 2em;
        right: 2em;
    }
    &__nav{
        display: flex;
        justify-content: center;
    }
    &__btn{
        margin: 0 .75em;
    }
}

.close-btn{
    width: 2.1875em;
    @include center;
    flex-shrink: 0;
    svg{
        width: 100%;
    }
}

.modal-form{
    @include grid100gap(1.25em);
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            padding: 1em;
            border-radius: .5em;
        }
        .close-btn{
            top: 1em;
            right: .75em;
        }
    }

    .close-btn{
        width: 1.875em;
    }
}
