.admin{
  padding: 8.25em 2.5em 2.5em 21.25em;
  height: $vh;
  &__scroll{
    height: 100%;
    overflow-y: auto;
    border-radius: 1.875em;
    background-color: $white;
    color: $black;
    padding: 2.5em;
    display: flex;
    flex-direction: column;
  }
  &__top{
    margin-bottom: 2.5em;
  }
  &__btn{
    margin-left: 2em;
    flex-shrink: 0;
    img{
      width: 1.5em;
      margin-left: .625em;
    }
  }
  &__back{
    width: 2.1875em;
    flex-shrink: 0;
    margin-left: 1em;
    &:hover{
      path{
        fill: $primary;
      }
    }
    svg{
      width: 100%;
    }
  }
}

.stat-card{
  padding: 1.25em 1.5em .3125em;
  border-radius: .625em;
  box-shadow: $shadow;
  &__top{
    padding: 0 .625em .625em;
  }
}

.stat-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .9375em .6875em;
  border-top: $border;
  &__info{
    @include grid100gap(.3125em);
  }
  &__price{
    white-space: nowrap;
  }
}

.stat-items2{
  padding-top: 2em;
  margin-top: auto;
}

.stat-card2{
  padding: .875em 1.25em;
  box-shadow: $shadow;
  border: $borderP;
  border-radius: .625em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__info{
    @include grid100gap(.625em);
  }
}


@media screen and (max-width: 992px){
  .admin{
    padding: 0;
    height: auto;
    &__scroll{
      border-radius: .5em;
      padding: 1em;
      height: auto;
      overflow: hidden;
    }
    &__top{
      margin-bottom: 1.5em;
      &:not(.admin__top--flex-mob){
        flex-direction: column;
        align-items: flex-start;
      }
    }
    &__btn{
      margin: 1em 0 0;
    }
  }

  .stat-card{
    padding-left: .75em;
    padding-right: .75em;
    &__top{
      padding-left: 0;
      padding-right: 0;
    }
  }

  .stat-item{
    padding-left: 0;
    padding-right: 0;
  }

  .stat-card2{
    padding: .75em;
    &__info{
      gap: .3125em;
    }
    &__num{
      white-space: nowrap;
    }
  }
}


@media screen and (max-width: 576px){
  .stat-card{
    .text20{
      font-size: 1.125em;
    }
  }
}
