.application-page{
  padding: 2.5em;
  background-color: $white;
  border-radius: 1.875em;
  box-shadow: $shadow;
  margin: 0 auto 2.5em;
  width: 75.5em;
  max-width: 100%;
  color: $black;
  display: grid;
  grid-template-columns: 39.5em calc(100% - 43em);
  gap: 2em;
  justify-content: space-between;
  .form-group{
    background-color: transparent;
    box-shadow: unset;
    padding: 0;
    &__inner{
      max-height: unset;
      overflow: unset;
      padding: 0;
    }
  }
  .h1{
    margin-bottom: 1.07em;
    text-align: center;
  }
  @media screen and (min-width: 993px){
    .application-info{
      margin-top: 6.25em;
    }
  }
}

.application-page2{
  display: block;
  width: 47em;
}


@media screen and (max-width: 992px){
  .application-page{
    margin-bottom: 0;
    border-radius: .5em;
    padding: 1em;
    grid-template-columns: 100%;
  }
}


