.calc{
  padding: 0 3.5em;
  display: grid;
  grid-template-columns: 39.5em 33.3125em;
  justify-content: space-between;
  gap: 2em;
  &__col2{
    padding-top: 2.5em;
  }
  &__fields{
    @include grid100gap(1.5em);
  }
  &__bottom{
    padding-top: .5em;
    margin-top: 2.25em;
    border-top: 1px solid #e6ebec;
  }
  &__sum{
    font-weight: 700;
    font-size: 4.875em;
    line-height: 150%;
    color: $primary;
  }
  &__props{
    margin-top: 1.875em;
  }
}

.range-block{
  &__output{
    padding: 0 .5em;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    box-shadow: 0 .1875em 1em rgba(0, 0, 0, 0.06);
    background-color: $white;
    height: 4.5625em;
    border-radius: .4375em .4375em 0 0;
    margin-top: .5625em;
    color: $secondary;
    &-text{
      font-size: 1.625em;
      line-height: 150%;
    }
  }
  .range-slider{
    border-radius: 0 0 .4375em .4375em;
  }
}

.diagram{
  max-width: 100%;
  width: 25.75em;
  margin: 0 auto;
  position: relative;
  &__img{
    width: 100%;
    display: block;
  }
  &__text{
    @include centerAbsolute;
    width: 100%;
    text-align: center;
  }
  &__title{
    font-weight: 700;
    font-size: 3.75em;
    line-height: 150%;
  }
}

.calc-prop{
  padding: 1em 0;
  display: flex;
  align-items: center;
  border-top: 1px solid #e6ebec;
  &:last-child{
    border-bottom: 1px solid #e6ebec;
  }
  &__price{
    margin-left: auto;
    padding-left: 1em;
  }
  &__icon{
    flex-shrink: 0;
    width: 1.5em;
    height: 1.5em;
    background-image: url(../img/bg2.svg);
    background-size: cover;
    border-radius: 50%;
    margin-right: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .calc{
    padding: 0;
    grid-template-columns: 100%;
  }
}


@media screen and (max-width: 576px){
  .calc{
    &__sum{
      font-size: 3em;
    }
  }

  .diagram{
    &__text{
      font-size: .75em;
    }
  }

  .calc-prop{
    @include grid100gap(.25em);
    &__price{
      margin: 0;
      padding: 0;
    }
  }
}
