.finance-co-table{
  th,td{
    min-width: 10em;
    &:nth-child(2){
      width: 26.25em;
    }
    &:nth-child(5){
      width: 20em;
    }
    &:last-child{
      width: 12.25em;
    }
  }
}

.shops-table{
  th,td{
    min-width: 10em;
    &:nth-child(2){
      width: 26.25em;
    }
    &:last-child{
      width: 22em;
    }
  }
}
