.users-table{
  th,td{
    min-width: 13.75em;
    &:nth-child(1){
      width: 13.75em;
    }
    &:nth-child(3){
      width: 11.25em;
      min-width: unset;
    }
    &:nth-child(4){
      width: 10em;
      min-width: unset;
    }
    &:last-child{
      width: 13.125em;
    }
  }
}
