.application{
  flex-grow: 1;
  display: grid;
  grid-template-columns: 47em 30.25em;
  gap: 2em;
  justify-content: space-between;
  padding: 0 2.5em;
  &__col{
    display: flex;
    flex-direction: column;
  }
  .form-group{
    flex-grow: 1;
    margin-top: 1.875em;
  }
  &__items{
    @include grid100gap(1.875em);
  }
  &__item{
    label{
      padding: 0;
      justify-content: space-between;
      &:before{
        order: 2;
        position: static;
        transform: unset;
        margin-left: .25em;
      }
      span{
        font-size: 1em;
      }
    }
  }
}

.form-group{
  padding: .5em .3125em;
  background-color: $white;
  box-shadow: $shadow;
  border-radius: .625em;
  display: flex;
  flex-direction: column;
  &__inner{
    flex-grow: 1;
    padding: 2em 3.375em;
    overflow-y: auto;
    @include scroll;
    max-height: calc(100 * var(--vh) - 25.875em);
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.25em 1.875em;
  }
}

._full{
  grid-column: 1/-1;
}

.work-experience{
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.25em 1.875em;
    margin-top: .625em;
  }
  label{
    font-size: .875em;
    font-weight: 400;
    margin-bottom: 1em;
  }
}

.filter-select{
  &:not(._open){
    .filter-select__dropdown{
      opacity: 0;
      pointer-events: none;
    }
  }
  &__current{
    cursor: pointer;
    background-image: url(../img/arrow.svg);
    background-repeat: no-repeat;
    background-position: right 1em center;
    background-size: 1.125em auto;
  }
  &__dropdown{
    transition: all .3s;
    position: absolute;
    top: 100%;
    right: 0;
    margin-top: .625em;
    background-color: $white;
    z-index: 3;
    padding: 1em 0;
    border-radius: .4375em;
    box-shadow: $shadow;
    max-height: 22em;
    overflow-y: auto;
    //min-width: 15.5em;
    width: 100%;
    max-width: 100%;
  }
  &__options{
    @include grid100gap(.25em);
  }
  &__option{
    label{
      margin: 0;
      font-weight: 400;
      position: static;
      display: flex;
      align-items: center;
      padding: 0 1.25em;
      height: 1.875em;
    }
    input:hover~label{
      background: #e5eef1;
    }
    input:checked~label{
      background: $primary;
      color: $white;
    }
  }
}

.application2{
  @media screen and (min-width: 993px){
    padding: 0 4.625em;
    grid-template-columns: 47em 26em;
  }
  .form-group{
    margin-top: 0;
  }
}

.application-info{
  padding: 1.25em;
  border-radius: .625em;
  background-color: $white;
  box-shadow: $shadow;
  &__items{
    @include grid100gap(1em);
  }
  &__item{
    @include grid100gap(.625em);
  }
  &__props{
    margin-top: 1em;
  }
  &__prop{
    padding-top: .4375em;
    margin-top: .625em;
    display: flex;
    justify-content: space-between;
    border-top: 2px solid $secondary;
    &:first-child{
      margin-top: 0;
      padding-top: 0;
      border-top: none;
    }
  }
}

.guarantor-application{
  padding: 1.5em 2.5em 0;
  display: grid;
  grid-template-columns: 39.5em 39.5em;
  justify-content: space-between;
  gap: 1.25em;
  &__fields{
    @include grid100gap(1.25em);
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__btn{
    margin-bottom: .625em;
  }
  &__hint{
    a{
      color: $black;
      font-weight: 700;
    }
  }
}


@media screen and (max-width: 992px){
  .application{
    padding: 0;
    grid-template-columns: 100%;
    &__items{
      gap: 1em;
    }
    .form-group{
      margin-top: 1em;
    }
  }

  .form-group{
    &__inner{
      max-height: unset;
      overflow: unset;
      padding: 1em .5em;
    }
  }

  .application-info{
    padding: 1.5em .8125em;
  }

  .guarantor-application{
    padding: 0;
    grid-template-columns: 100%;
  }
}


@media screen and (max-width: 576px){
  .work-experience{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
