.create-request{
  padding: 0 6.25em 0 1.25em;
  display: grid;
  grid-template-columns: 39.5em 35.25em;
  gap: 2em;
  justify-content: space-between;
  flex-grow: 1;
  &__col{
    display: flex;
    flex-direction: column;
  }
  &__fields{
    @include grid100gap(1.25em);
  }
  &__nav{
    padding-top: 2em;
    margin-top: auto;
    display: flex;
    justify-content: center;
  }
  &__qr{
    @include center;
    padding: 6.25em;
    border-radius: .625em;
    background-color: $white;
    box-shadow: $shadow;
    margin-top: .625em;
    img{
      max-width: 23em;
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .create-request{
    padding: 0;
    grid-template-columns: 100%;
  }
}


@media screen and (max-width: 576px){
  .create-request{
    &__qr{
      padding: 3em;
    }
  }
}
