.filter-slider{
  @include lastM(0 0 .75em);
  &__item{
    //min-height: 1.5625em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: $secondary;
    border: none;
    box-shadow: none;
    height: .375em;
    border-radius: .1875em;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: .4375em solid $primary;
      background-color: $white;
      width: 1.5625em;
      height: 1.5625em;
      top: -.59375em !important;
      right: -.59375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__nums{
    display: flex;
    justify-content: space-between;
    margin-top: 1.125em;
  }
}
