@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.25em;
    height: 2.5em;
    border-radius: .3125em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
    }
}

.m-btn-black{
    background-color: $black;
    color: $white;
    &:hover{
        background-color: darken($primary,30%);
        color: $white;
    }
}

.m-btn-red{
    background-color: #f71735;
    color: $white;
    &:hover{
        background-color: darken(#f71735,30%);
        color: $white;
    }
}
