.request-item{
  display: grid;
  grid-template-columns: 39.5em 39.5em;
  justify-content: space-between;
  gap: 2em;
  padding: 0 2.25em;
  &__fields{
    @include grid100gap(1.25em);
  }
  .range-block{
    margin-top: 1.25em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 2em;
    display: flex;
    justify-content: flex-end;
  }
}

.pay-dates{
  margin-top: .75em;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180.00deg, rgba(255, 255, 255, 0) 45.5%,rgb(255, 255, 255) 100%);
  }
  &__desc{
    margin-top: .625em;
  }
  &__items{
    margin-top: .625em;
    @include grid100gap(.25em);
  }
}

.pay-item{
  cursor: pointer;
  height: 6em;
  display: flex;
  align-items: flex-start;
  position: relative;
  &._gray{
    .pay-item__num,
    &:before{
      background-color: $secondary;
    }
  }
  &:before{
    content: '';
    position: absolute;
    height: 3.875em;
    width: .125em;
    bottom: 0;
    left: .9375em;
    transform: translateX(-50%);
    background-color: $primary;
    border-radius: .0625em;
  }
  &__num{
    flex-shrink: 0;
    width: 1.875em;
    height: 1.875em;
    @include center;
    border-radius: 50%;
    background-color: $primary;
    color: $white;
    margin-right: 2.1875em;
  }
  &__content{
    padding-top: .25em;
    @include grid100gap(.25em);
  }
}

.range-block2{
  ._output{
    display: none;
  }
  .filter-slider{
    margin-top: 1.125em;
  }
  .range-slider{
    .noUi-handle{
      &:after{
        content: attr(data-value);
        display: block;
        left: 50%;
        top: 100%;
        transform: translateX(-50%);
        font-size: 1em;
        line-height: 150%;
        font-weight: 700;
        background: $white;
        padding: 0 2em;
        width: auto;
        height: auto;
        margin-top: .9375em;
        color: $black;
        opacity: 1;
        font-family: $font1;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .request-item{
    grid-template-columns: 100%;
    padding: 0;
    &__nav{
      justify-content: center;
    }
  }
}


