.settings{
  &__grid{
    display: grid;
    grid-template-columns: 39.5em 39.5em;
    justify-content: space-between;
    gap: 1.25em;
    margin-top: 2.5em;
  }
  &__fields{
    @include grid100gap(2.5em);
  }
  &__btn{
    display: inline-flex;
    margin-right: auto;
  }
}


@media screen and (max-width: 992px){
  .settings{
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
    &__fields{
      gap: 1.25em;
    }
    &__btn{
      width: 100%;
    }
  }
}


