.auth{
  padding: 15em 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  .header__logo{
    margin: 0;
  }
  &__inner{
    padding: 2.5em 2.875em;
    border-radius: 1.25em;
    background-color: $white;
    box-shadow: $shadow;
    margin: 2.625em auto 0;
    width: 25.25em;
    max-width: 100%;
    color: $black;
  }
}

.auth-form{
  margin-top: 1.875em;
  &__fields{
    @include grid100gap(1.125em);
  }
  &__btn{
    width: 100%;
    margin-top: 1.125em;
  }
  &__bottom{
    display: flex;
    justify-content: center;
    margin-top: 2em;
  }
  &__link{
    font-size: .75em;
    line-height: 122%;
    text-decoration: underline;
  }
}


@media screen and (max-width: 992px){
  .auth{
    padding: 1em 0;
    &__inner{
      padding: 1.5em 1em;
      border-radius: .5em;
      margin-top: 1em;
    }
  }
}


