.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 2.5em 2.5em 2em;
  z-index: 10;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    width: 13em;
    display: block;
    flex-shrink: 0;
    margin-right: 5.75em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__greet{
    margin-left: auto;
  }
}

.header-search{
  width: 48.375em;
  position: relative;
  input{
    width: 100%;
    height: 2.55em;
    padding: 0 2.67em 0 .67em;
    border: 2px solid $white;
    background-color: transparent;
    border-radius: .83em;
    font-size: 1.125em;
    color: $white;
    &::placeholder{
      color: $secondary;
    }
  }
  &__btn{
    position: absolute;
    right: .75em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.5em;
    @include center;
    &:hover{
      opacity: .8;
    }
    img{
      width: 100%;
      display: block;
    }
  }
}

.sidebar-menu{
  position: fixed;
  top: 10.75em;
  left: 2.5em;
  height: calc(100 * var(--vh) - 15.75em);
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  &__links{
    padding-top: 1.875em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__links2{
    margin-top: auto;
  }
  &__link{
    display: flex;
    align-items: center;
    margin-bottom: 1.25em;
    &:last-child{
      margin-bottom: 0;
    }
    &._active,
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    svg{
      width: 1.5em;
      margin-right: 1.25em;
    }
    span{
      font-size: 1em;
      line-height: 125%;
    }
  }
}

.burger{
  display: none;
}

.header2{
  position: relative;
  z-index: 2;
  @include center;
  padding: 2.5em 1em;
  .header__logo{
    margin: 0;
  }
}



@media screen and (max-width: 992px){
  .header{
    position: static;
    padding: 1.5em 1em;
    &__greet{
      display: none;
    }
    &__logo{
      width: 10em;
      position: relative;
      z-index: 12;
      margin: 0;
    }
    &._open{
      .mob-menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .mob-menu{
    z-index: 11;
    position: fixed;
    top: 0;
    left: 0;
    background-color: $black;
    width: 100%;
    height: $vh;
    overflow-y: auto;
    padding: 5.125em 1em 2em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }

  .sidebar-menu{
    position: static;
    height: auto;
    margin-top: 3em;
    &__links2{
      padding-top: 3sem;
    }
  }

  .header-search{
    width: 100%;
  }

  .burger{
    z-index: 12;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    margin-left: auto;
    svg,
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 90%;
    }
  }

  .header2{
    padding: 1.5em 1em;
  }
}


