.admin-filter{
  margin-bottom: 1.875em;
  &__search{
    width: 37.5em;
    position: relative;
    input{
      width: 100%;
      height: 2em;
      padding: 0 2.5em 0 .5em;
      border: 2px solid $black;
      background-color: transparent;
      border-radius: .5em;
      font-size: 1.125em;
      color: $black;
      &::placeholder{
        color: $secondary;
      }
    }
    &-btn{
      position: absolute;
      right: .5em;
      top: 50%;
      transform: translateY(-50%);
      width: 1.1875em;
      @include center;
      &:hover{
        opacity: .8;
      }
      img{
        width: 100%;
        display: block;
      }
    }
  }
}

.sort{
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: all .3s;
  &:hover{
    color: $primary;
    path{
      stroke: $primary;
    }
  }
  span{
    font-size: 1em;
    line-height: 150%;
  }
  svg{
    width: 1.5em;
    margin-left: 1.625em;
  }
}

.m-table{
  table{
    width: 100%;
    border-spacing: 0 1.25em;
  }
  th,td{
    padding-left: .625em;
    padding-right: .625em;
  }
  th{
    text-align: left;
    font-weight: 700;
    padding-bottom: .625em;
  }
  td{
    position: relative;
    vertical-align: top;
    padding-top: 1.25em;
    padding-bottom: 1.25em;
    border: 1px solid $white;
    transition: all .3s;
    &:first-child{
      border-radius: .625em 0 0 .625em;
    }
    &:last-child{
      border-radius: 0 .625em .625em 0;
      padding-right: 2em;
    }
    &:not(:first-child){
      border-left: none;
    }
    &:not(:last-child){
      border-right: none;
    }
  }
  tbody{
    tr{
      background-color: $white;
      box-shadow: $shadow;
      border-radius: .625em;
      &:hover{
        td{
          z-index: 2;
          border-color: $primary;
        }
        .row-action{
          opacity: 1;
        }
      }
    }
  }
  .text16{
    margin-top: .5em;
    &:first-child{
      margin-top: 0;
    }
  }
}

.debtor-table{
  th,td{
    &:nth-child(1){
      max-width: 9em;
    }
    &:last-child{
      width: 16em;
    }
  }
}

.m-badge{
  display: inline-flex;
  align-items: center;
  padding: .375em .5em;
  min-height: 2.25em;
  background: #f0f5f7;
  border-radius: .25em;
  &._yellow{
    background: #FFFDD7;
  }
  &._red{
    background: #FFE3DA;
  }
  &._green{
    background: #DDFFEF;
  }
}

.row-action{
  position: absolute;
  top: .5em;
  right: .5em;
  @media screen and (min-width: 993px){
    opacity: 0;
    transition: all .3s;
  }
  &._open{
    .row-action__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__btn{
    width: 1.5em;
    @include center;
    svg{
      width: 100%;
    }
  }
  &__dropdown{
    position: absolute;
    top: 100%;
    right: 0;
    margin-top: .5em;
    width: 15.5em;
    border-radius: .5em;
    background-color: $white;
    padding: 1em 0;
    box-shadow: $shadow;
    @include grid100gap(.25em);
    opacity: 0;
    pointer-events: none;
  }
  &__item{
    display: flex;
    align-items: center;
    padding: 0 1.25em;
    height: 1.875em;
    &:hover{
      background: #e5eef1;
      color: $black;
    }
  }
}


@media screen and (max-width: 992px){
  .admin-filter{
    flex-direction: column;
    margin-bottom: 1em;
    &__search{
      width: 100%;
    }
    .sort{
      margin: 1em auto 0 0;
    }
  }

  .sort{
    font-size: .875em;
  }

  .m-table{
    overflow-x: auto;
    overflow-y: hidden;
    padding: 0 1em 2em;
    margin-left: -1em;
    width: calc(100% + 2em);
    table{
      min-width: 91.25em;
      font-size: .75em;
    }
    th{
      padding-bottom: 0;
    }
  }
}


